
.App {
  text-align: center;
}


.create-lobby-page b {
  display: block;
  width: 270px;
  margin: auto;
  margin-top: 200px;
}
.join-container {
  padding: 20px
}

.join-container input {
  margin-bottom: 20px;
}


.home-btn {
  display: block;
  width: 50%;
  margin: 20px auto;
}
.home-wrapper h1 {
  margin-bottom: 40px
}
.home-wrapper {
  display: block;
  width: 500px;
  margin: auto;
  margin-top: 20vh
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
